@import '../variables';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;

  @media (max-width: 670px) {
    padding: 20px 0 10px 0;
  }

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 870px) {
      flex-direction: column;

      .header__cart {
        margin-top: 10px;
      }
    }
  }

  &__logo {
    display: flex;
    padding: 5px;
    margin-bottom: 5px;
    border-radius: 10px;

    
    &:hover {
      background-color: #efefef;
    }
    
    img {
      margin-right: 15px;
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1px;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
      margin-bottom: 5px;
    }
  }
}